<template>
	<div class="page-content-body hospital">
		<CompanyBar />
		<div class="company-bar"></div>

		<div class="hospital-body">
			<!-- <scroller class="scroller"
				:on-infinite="onInfinite"
				ref="scroller">
				<CompanyHeader :companySwiper="companySwiper" />

				<div class="tabs-striped-content-items">
					<router-link tag="div" class="tabs-striped-content-item" :to="{'path': '/detail-hospital', query: { id: item.id }}" v-for="item in items">
						<img :src="item.headimg">

						<div class="img-info">
							<h2>{{item.name}}</h2>
							<div class="item-info">{{item.content[0]}}</div>
						</div>
					</router-link>
				</div>
			</scroller> -->

			<CompanyHeader :companySwiper="companySwiper" />

			<div class="hospital-body-items">
				<router-link tag="div" class="hospital-body-item" :to="{ path: '/hospital-detail',query: { id: item.id } }" v-for="item in items">
					<img :src="item.headimg">

					<div class="img-info">
						<h2>{{item.name}}</h2>
						<div class="item-info">{{item.content[0]}}</div>
					</div>
				</router-link>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import qs from 'qs'
	import CompanyBar from '@/components/CompanyBar.vue'
	import CompanyHeader from '@/components/CompanyHeader.vue'

	// 模拟数据
	import Hospital from '../data-mock/hospital.json'

	export default {
		name: 'hospital',
		components: { CompanyBar, CompanyHeader },
		data() {
			return {
				companySwiper: {
					titleName: '合作医院',
					titleInfo: '日本尖端医疗机构',
					className: 'hospital'
				},
				items: Hospital.data.list,
				scrollNext: true,
			}
		},
		mounted() {
			sessionStorage.hospital = JSON.stringify(Hospital.data.list);
		},
		methods: {
			onInfinite() {
				// 到底部时加载新数据, 需要根据当前 tabIndex
				if (this.scrollNext) {
					fnHospital(this, {});
				}else{
					if (this.$refs.scroller) this.$refs.scroller.finishInfinite();
				}
			},
			openHospitalDetail(item) {
				this.$router.push({ name: 'hospital-detail', path: 'hospital-detail' });
			}
		}
	}

	function fnHospital(that, params) {
		axios.get(
			that.$api.hospital + qs.stringify(params),
			{
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				timeout: 300000
			}
		).then(response => {
			let result = response.data;

			if (result.code === 0) {
				if (that.items.length == 0) {
					that.items = result.data.list;
				}else{
					if (that.scrollNext) {
						that.items = _.concat(that.items, result.data.list);
					}
				}
				that.scrollNext = result.data.next;

				sessionStorage.hospital = JSON.stringify(that.items);

				setTimeout(() => {
					if (that.$refs.scroller) that.$refs.scroller.finishInfinite();
				})
			}
		}).catch(error => {
			if (error.response && error.response.data && error.response.data.code) {
				alert(error.response.data.msg)
			}
		});
	}
</script>

